import PetSearchForm from "@/components/search/PetSearchForm";
import SearchResults from "@/components/search/SearchResults";
import { Suspense } from "react";

export default function SearchPage({
  searchParams: query,
}: {
  searchParams: Record<string, string | string[] | undefined>;
}) {
  const filters = {
    type: query.type as string | undefined,
    gender: query.gender as string | undefined,
    size: query.size as string | undefined,
    age: query.age as string | undefined,
  };

  return (
    <>
      <PetSearchForm />

      <Suspense fallback={<p className="text-center py-10">Loadingâ€¦</p>}>
        <SearchResults {...filters} />
      </Suspense>
    </>
  );
}
